<template>
  <div class="member flex flex-column page container-full">
    <div class="banner flex jc-between">
      <div class="statistics">
        <p class="text">{{ language.tip_5_1 }}：</p>
        <h1 class="rate gold"><b>{{ totalAPY.times(100).toFixed(2) }}%</b></h1>
        <p class="text">{{ language.tip_5_2 }}：</p>
        <p class="num-box flex ai-end">
          <vip-number class="num" v-for="(num,idx) in totalDisplayArr" :number="num" :key="idx"/>
        </p>
      </div>
      <div class="mid flex jc-center ai-end">
        <img src="../../images/member/layer.png" alt="">
      </div>
      <div class="casting flex flex-column jc-center fs-14">
        <div>
          <h1 class="gold">{{ language.cast }} DFV</h1>
          <p class="p1">{{ language.tip_5_3 }}</p>
          <p class="p2">{{ language.tip_5_4 }}：</p>
          <p class="p-tip">* {{ language.bonus + language.reward }}：{{ language.tip_5_5 }}</p>
          <p class="p-tip">* {{ language.menu_5 + language.reward }}：{{ language.tip_5_6 }}</p>
          <p class="p-tip">* NFT{{ language.airdrop }}：{{ language.tip_5_7 }}</p>
          <p class="p-tip">* {{ language.label_5_1 }}：{{ language.tip_5_8 }}</p>
        </div>
        <div class="huge-btn flex jc-center ai-center">
          <div class="b1 flex jc-center ai-center fs-20" @click="showPopper(1)">
            {{ language.cast }} DFV
          </div>
          <div class="b2 flex jc-center ai-center gold fs-18" @click="showPopper(0)">
            {{ language.redeem }}
          </div>
        </div>
      </div>
    </div>
    <ul class="mine flex">
      <li class="item-box">
        <header class="flex jc-between ai-center">
          <span class="text">{{ language.my }}DFV</span>
          <m-button class="item-btn border-red hdr-el-red red" @click="showPopper(0)" :height="36">
            {{ language.redeem }}
          </m-button>
        </header>
        <div class="flex ai-center">
          <span><b class="amount gold">{{ balanceFormatter(balance.DFV) }}</b> <br>DFV</span>
        </div>
      </li>
      <li class="item-box">
        <header class="flex jc-between ai-center">
          <span class="text">{{ language.label_5_1 }}</span>
          <m-button v-if="unclaimedShare.isZero()" class="item-btn border-gray hdr-el-gray gray" :disabled="true"
                    :height="36">
            {{ language.btn_reap }}
          </m-button>
          <m-button v-else class="item-btn border-green hdr-el-green green" @click="reapShare" :loading="reapLoading"
                    :height="36">
            {{ language.btn_reap }}
          </m-button>
        </header>
        <div class="flex ai-center">
          <span>
            <b class="amount text">{{ amountFormatter(airInfo.shareClaimedAmountTotal, 4) }}</b> <span>DFV</span><br>
            <span>{{ language.order_unclaimed }}：<span class="text">{{ unclaimedShare.myFixed() }}</span></span>
          </span>
        </div>
      </li>
      <li class="item-box">
        <header class="flex jc-between ai-center">
          <span class="text">{{ language.bonus + language.reward }}</span>
        </header>
        <div class="flex ai-center">
          <span><b class="amount text">{{ amountFormatter(rewardStatistics.bonusReward, 4) }}</b> <br>DFV</span>
        </div>
      </li>
      <li class="item-box">
        <header class="flex jc-between ai-center">
          <span class="text">{{ language.menu_5 + language.reward }}</span>
        </header>
        <div class="flex ai-center">
          <span><b class="amount text">{{ amountFormatter(rewardStatistics.vipReward, 4) }}</b> <br>DFV</span>
        </div>
      </li>
      <li class="item-box">
        <header class="flex jc-between ai-center">
          <span class="text">{{ language.airdrop + language.reward }}</span>
        </header>
        <div class="flex ai-center">
          <span><b class="amount text">{{ airInfo.airDropCount }}</b> <span></span></span>
        </div>
      </li>
    </ul>
    <m-popper v-model="showDialog">
      <template v-slot:title>
        {{ actionType ? language.cast + ' DFV' : language.redeem + ' DFT' }}
      </template>
      <div class="main-box">
        <!--        输入框-->
        <div class="input-bar">
          <p class="flex jc-between ai-center">
            <b class="flex ai-center text">
              <img class="coin icon-s" :src="coinMap[actionType?'DFT':'DFV']" alt="">{{ actionType ? 'DFT' : 'DFV' }}
            </b>
            <span>{{ language.wallet_balance }}：<span class="text">{{ balanceFormatter(maxAmount) }}</span></span>
          </p>
          <label class="input-row flex jc-between ai-center">
            <input class="flex1" type="number" v-model="amountDisplay" @input="amountInput"
                   :placeholder="language.tip_2_3">
            <span class="max-btn green" @click="setAmountMax()">MAX</span>
          </label>
        </div>
        <template v-if="actionType">
          <label v-if="mySuperior===''" class="invitation-row flex ai-center">
            <input class="flex1" type="text" v-model="superAddress" :placeholder="language.tip_5_10">
            <span>{{ language.label_5_5 }}</span>
          </label>
        </template>
        <p v-else class="flex jc-between ai-center">
          <span>{{ language.label_5_2 }}</span>
          <span class="red">{{ feeRatio.times(100).toFixed(1) }}%</span>
        </p>
        <p class="flex jc-between ai-center">
          <span>{{ language.label_5_3 + language.ratio }}</span>
          <span class="text">1 DFV = 100 DFT</span>
        </p>
        <p class="flex jc-between ai-center">
          <span>{{ language.label_5_4 }}</span>
          <span class="text">{{ expectedNum }} {{ actionType ? 'DFV' : 'DFT' }}</span>
        </p>
        <div v-if="!actionType" class="redeem-tip">{{ language.tip_notice }}* {{ language.tip_5_9 }}</div>
        <p class="btn-row flex jc-center">
          <template v-if="isAllowed">
            <m-button v-if="buyAmount.gt(0)" class="action-btn hdr-el-blue text" @click="actionHandle" :loading="actionLoading">
              {{ actionType ? language.cast : language.redeem }}
            </m-button>
            <m-button v-else class="action-btn hdr-el-gray border-gray gray" :disabled="true">
              {{ actionType ? language.cast : language.redeem }}
            </m-button>
          </template>
          <m-button v-else class="action-btn hdr-el-blue text" @click="doAllowance" :loading="allowLoading">
            {{ language.btn_authorize }}
          </m-button>
        </p>
      </div>
    </m-popper>
  </div>
</template>

<script>
import {mapGetters, mapState} from "vuex";
import VipNumber from "@/views/vip/comps/vipNumber";
import MButton from "@/components/mButton";
import MPopper from "@/components/mPopper";
import {coinMap} from "@/common/walletConfig";
import {BN} from "@/common/util";
import {amountFormatter, balanceFormatter, inputNumberFixer} from "@/common/dataConvertor";

export default {
  name: "member",
  components: {MPopper, MButton, VipNumber},
  watch: {
    address() {
      this.userInit();
    }
  },
  computed: {
    ...mapGetters(['language']),
    ...mapState('app', ['address', 'balance']),
    totalDisplayArr() {
      let str = '0000';
      let totalUsers = this.vDEFX?.totalUsers || str;
      if (totalUsers.length < str.length) {
        totalUsers = str.substr(0, str.length - totalUsers.length) + totalUsers;
      }
      return totalUsers.split('')
    },
    rewardStatistics() {
      let {putDft, userFactor, vDEFX, alphaInfo, totalBlockDistribution} = this;
      if (!userFactor || !vDEFX || !alphaInfo) return {};
      let dftReward = userFactor ? putDft.plus(userFactor.redeemRecieveAmount).minus(userFactor.mintAmount) : new BN(0);
      if(dftReward.lt(0)) {
        dftReward = new BN(0);
      }
      let allReward = vDEFX && alphaInfo ? totalBlockDistribution.plus(alphaInfo.curDistribution / 1e18).plus(vDEFX.totalDonate).plus(vDEFX.feeAmount) : new BN(0);
      let vipReward = totalBlockDistribution.plus(alphaInfo.curDistribution / 1e18).times(dftReward).div(allReward);
      
      let bonusReward = dftReward.times(vDEFX.totalDonate).div(allReward);
      let feeReward = dftReward.times(vDEFX.feeAmount).div(allReward);
      // console.debug('dftReward =====> ', dftReward.toString(10))
      // console.debug('allReward =====> ', allReward.toString(10))
      // console.debug('vipReward =====> ', vipReward.toString(10))
      // console.debug('bonusReward =====> ', bonusReward.toString(10))
      // console.debug('feeReward =====> ', feeReward.toString(10))
      return {
        dftReward: dftReward,
        allReward: allReward.div(100),
        vipReward: vipReward.div(100),
        bonusReward: bonusReward.div(100),
        feeReward: feeReward.div(100)
      }
    },
    maxAmount() {
      return this.balance[this.actionType ? 'DFT' : 'DFV'];
    },
    buyAmount() {
      let {amountDisplay} = this;
      return amountDisplay ? new BN(amountDisplay).toWei('balance') : new BN(0);
    },
    expectedNum() {
      let {actionType, amountDisplay} = this;
      let rate = actionType ? new BN(0.01) : new BN(1).minus(this.feeRatio).times(100);
      return rate.times(amountDisplay||0).toFixed(2);
    }
  },
  data() {
    return {
      coinMap,
      baseAPY: new BN(0),
      feeAPY: new BN(0),
      totalAPY: new BN(0),  //年化总收益
      totalBlockDistribution: new BN(0),
      unclaimedShare: new BN(0),
      reapLoading: false,
      putDft: new BN(0),
      vDEFX: null,
      alphaInfo: null,
      userFactor: null,
      airInfo: {
        airDropCount: '0',
        shareClaimedAmountTotal: new BN(0),
      },
      showDialog: false,
      actionType: 0,
      actionLoading: false,
      amountDisplay: '',
      feeRatio: new BN(0),
      superAddress: '',
      mySuperior: '',
      isAllowed: false,
      allowLoading: false,
    }
  },
  methods: {
    balanceFormatter,
    amountFormatter,
    userInit() {
      let address = this.address;
      this.$api.getBaseInfo().then(([feeRatio, totalBlockDistribution]) => {
        this.feeRatio = new BN(feeRatio).div(1e18);
        this.totalBlockDistribution = new BN(totalBlockDistribution).div(1e18);
      })
      if (!address) return;
      this.$api.vipCheckAllowance(address).then(num => this.isAllowed = num !== '0');
      this.$api.getVipUnclaimedShare(address).then(num => this.unclaimedShare = new BN(num).div(1e18).div(100));
      this.$api.vipUserInfo(address).then(({users}) => this.userFactor = users[0]);
      this.$api.getVipUserInfo(address).then(([mySuperior, putDft]) => {
        this.mySuperior = mySuperior === '0x0000000000000000000000000000000000000000' ? '' : mySuperior;
        this.putDft = new BN(putDft).div(1e18);
      });
      this.$api.getAirInfo(address).then(({users}) => {
        let user = users[0];
        if (user) {
          let {shareClaimedAmountTotal} = user;
          shareClaimedAmountTotal = new BN(shareClaimedAmountTotal).div(100)
          this.airInfo = {...user, shareClaimedAmountTotal}
        }
      });
    },
    getFactors() {
      let p1 = this.$api.vipvDefxInfo();
      let p2 = this.$api.getLatestAlpha();
      let p3 = this.$api.vipDonateRec3();
      let p4 = this.$api.vipRedeemRec3();
      const day_seconds = 86400;
      const year_blocks = (day_seconds / 3) * 365;
      Promise.all([p1, p2, p3, p4]).then(([{vDEFX}, alphaInfo, {donateHistories}, {redeemHistories}]) => {
        if (vDEFX===null) return;
        let {defxPerBlock, totalStakingPower, totalUsers} = this.vDEFX = vDEFX;
        let {newAlpha} = this.alphaInfo = alphaInfo;
        totalStakingPower = new BN(totalStakingPower);
        let rewardYear = new BN(defxPerBlock * year_blocks).times(1e18); //一年预计发放的基础奖励
        let stakedDefx = totalStakingPower.times(newAlpha).div(1e18); //用户总质押的dft(包含信用额度)
        let baseAPY = rewardYear.div(stakedDefx); //基础收益率

        let donateAmount3 = donateHistories.reduce((sum, {defxAmount}) => sum + new Number(defxAmount), 0);
        let donateDt = 0, donateYear = 0;
        if (donateHistories.length > 1) {
          donateDt = donateHistories[0].timestamp - donateHistories[donateHistories.length - 1].timestamp;
          donateYear = new BN(donateAmount3).times(1e18).div(donateDt / day_seconds).times(365); //预计一年的分红收益
        }
        let donateAPY = new BN(donateYear).div(stakedDefx); //分红收益率

        let feeAmount3 = redeemHistories.reduce((sum, {recieve}) => sum + new Number(recieve), 0); //手续费总和
        console.info(`feeAmount3 = ${feeAmount3}`);
        let feeDt = 0, feeYear = 0;
        if (redeemHistories.length > 1) {
          feeDt = redeemHistories[0].timestamp - redeemHistories[redeemHistories.length - 1].timestamp;
          feeYear = new BN(feeAmount3).times(1e18).div(feeDt / day_seconds).times(365); //预计一年的手续费收益
        }
       
        let feeAPY = new BN(feeYear).div(stakedDefx); //手续费收益率

        this.totalUsers = totalUsers;
        this.baseAPY = baseAPY;
        this.feeAPY = feeAPY;
     
        console.debug('======', baseAPY.toString(10))
        console.debug('======', donateAPY.toString(10))
        console.debug('======', feeAPY.toString(10))
        this.totalAPY = baseAPY.plus(donateAPY).plus(feeAPY); //总apy
      });
    },
    amountInput(e) {
      let val = e.target.value;
      let max = this.maxAmount.toDisplay('balance');
      val = Number(val) > max ? max.toString(10) : val;
      val = inputNumberFixer(val);
      this.amountDisplay = val;
    },
    showPopper(actionType) {
      this.actionType = actionType;
      this.amountDisplay = '';
      this.showDialog = true;
      this.checkInviter();
    },
    setAmountMax() {
      this.amountDisplay = inputNumberFixer(this.maxAmount.toDisplay('balance').toString(10));
    },
    doAllowance() {
      this.allowLoading = true;
      this.$api.vipApprove().then(() => {
        this.isAllowed = true;
        this.allowLoading = false;
      }).catch(() => this.allowLoading = false);
    },
    actionHandle() {
      let {buyAmount, $api, actionType, superAddress} = this;
      debugger
      buyAmount = buyAmount.toString(10);
      let promise = actionType ? $api.vipCast(buyAmount, superAddress) : $api.vipRedeem(buyAmount);
      this.actionLoading = true;
      promise.on('transactionHash', () => {

      }).then(() => {
        this.$api.getBalance();
        this.actionLoading = false;
        this.showDialog = false;
      }).catch(() => {
        this.actionLoading = false;
      })
    },
    reapShare() {
      if (!this.address) return;
      this.reapLoading = true;
      this.$api.claimVipShare(this.address).then(() => {
        this.reapLoading = false;
        this.$api.getBalance();
        this.userInit();
      }).catch(() => this.reapLoading = false)
    },
    checkInviter(){
      let sp = this.$route.query.inviter;
      if (!this.mySuperior&&sp) this.superAddress = sp;
    }
  },
  mounted() {
    this.getFactors();
    this.userInit();
  },
}
</script>

<style lang="less" scoped>
.member {
  padding: 20px 80px;
  color: #8C8F96;
}
.gold {
  color: #F4DAA9;
}
h1 {
  margin-top: 0;
}
.banner {
  padding: 20px 30px;
  background-color: #1F1F1F;
  border-radius: 10px;
  .statistics {
    width: 280px;
    padding-top: 30px;
    .rate {
      margin-bottom: 80px;
      font-size: 40px;
    }
    .num-box {
      margin-top: 30px;
    }
  }
  .mid {
    margin: 0 5%;
    img {
      width: 100%;
      max-width: 490px;
      min-width: 260px;
      margin-top: 50px;
    }
  }
  .casting {
    width: 40%;
    max-width: 460px;
    p {
      line-height: 1.6;
    }
    .p2 {
      margin-top: 18px;
    }
    .huge-btn {
      margin-top: 40px;
      .b1 {
        width: 185px;
        height: 56px;
        padding-left: 15px;
        position: relative;
        background: linear-gradient(180deg, #F5DBAA 0%, #D6B681 100%);
        border-radius: 24px 0 0 24px;
        color: #000;
        &:hover {
          color: #333;
        }
        cursor: pointer;
        &:after {
          content: "";
          position: absolute;
          width: 20px;
          height: 100%;
          top: 0;
          right: 0;
          transform-origin: 0 100%;
          transform: skewX(-18deg);
          background: inherit;
        }
      }
      .b2 {
        width: 165px;
        height: 52px;
        background: linear-gradient(180deg, #515253 0%, #333435 100%);
        border-radius: 0 24px 24px 0;
        cursor: pointer;
        &:hover {
          color: #f3c779;
        }
      }
    }
  }
  .num {
    width: 35px;
    margin-right: 10px;
  }
}
ul {
  flex-wrap: wrap;
}
.item-box {
  width: 19%;
  min-width: 250px;
  margin-top: 20px;
  //height: 170px;
  padding: 0 15px;
  line-height: 40px;
  background: #191F2D;
  border-radius: 10px;
  position: relative;
  &:not(:last-child) {
    margin-right: 1.25%;
  }
  header {
    height: 60px;
  }
  .item-btn {
    min-width: 70px;
  }
  .amount {
    line-height: 40px;
    font-size: 25px;
    & + span {
      margin-left: 5px;
    }
  }
}
//赎回
.main-box {
  width: 400px;
  padding: 10px 0 30px;
  .input-bar {
    margin-bottom: 10px;
    padding: 10px 20px 15px;
    background-color: #353B4D;
    border-radius: 10px;
    .coin {
      margin-right: 10px;
    }
  }
  .input-row {
    height: 40px;
    margin-top: 10px;
    padding: 0 15px;
    background-color: #191f2d;
    border-radius: 10px;
    .max-btn {
      padding-left: 15px;
      cursor: pointer;
    }
  }
  .invitation-row {
    height: 40px;
    margin: 20px 0 15px;
    padding: 0 20px;
    background-color: #353B4D;
    border-radius: 10px;
  }
  p {
    height: 40px;
  }
  .redeem-tip {
    margin-top: 10px;
    line-height: 1.6;
  }
  .btn-row {
    margin-top: 25px;
  }
  .action-btn {
    width: 120px;
  }
}
@media screen and (max-width: 1024px) {
  .member {
    padding: 20px 30px;
    height: auto;
  }
  .mine {
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .item-box {
    width: calc(50% - 10px);
    height: auto;
    min-width: auto;
    line-height: 30px;
    padding: 0 20px;
    margin-top: 20px;
    margin-right: 0 !important;
  }
  .banner{
    flex-flow: wrap;
    padding-bottom: 30px;
    .statistics{
      width: 50%;
      .rate{
        margin-top: 20px;
      }
    }
    .mid{
      width: 50%;
      margin: 0;
    }
    .casting{
      width: 100%;
      max-width: none;
    }
  }
}
@media screen and (max-width: 768px) {
  .banner{
    .statistics{
      padding-top: 10px;
      .rate {
        margin-bottom: 50px;
        font-size: 30px;
      }
    }
    .casting{
      margin-top: 30px;
    }
  }
}
@media screen and (max-width: 540px) {
  .member {
    padding: 0 15px;
    height: auto;
    flex-direction: column-reverse;
  }
  .mine {
    margin-top: 0;
    flex-wrap: wrap;
  }
  ul {
    justify-content: space-between;
  }
  .item-box {
    width: calc(50% - 5px);
    line-height: 30px;
    padding: 0 10px;
    margin-top: 10px;
    margin-right: 0 !important;
    &:first-child, &:nth-child(2) {
      width: 100%;
    }
    header {
      height: 40px;
    }
    .amount {
      line-height: 30px;
      font-size: 16px;
    }
    .item-btn {
      min-width: 60px;
      height: 30px !important;
      position: absolute;
      top: 10px;
      right: 10px;
      font-size: 12px;
    }
  }
  h1 {
    font-size: 20px;
    margin-top: 25px;
  }
  .banner {
    height: auto;
    padding: 20px 15px;
    margin-top: 10px;
    margin-bottom: 10px;
    .statistics {
      padding-top: 0;
      .rate {
        margin-bottom: 20px;
        font-size: 20px;
      }
      .num-box {
        margin-top: 20px;
      }
    }
    .mid {
      margin: 0;
      img {
        margin-top: 30px;
      }
    }
    .casting {
      width: 100%;
      margin-top: 0;
      max-width: none;
      min-width: auto;
      flex-direction: column-reverse;
      .huge-btn {
        margin-top: 20px;
      }
    }
    .num {
      margin-right: 5px;
    }
  }
  .main-box {
    width: calc(100vw - 80px);
    .input-bar {
      margin-bottom: 10px;
      padding: 10px 15px;
      background-color: #353B4D;
      border-radius: 10px;
      p {
        height: 40px;
        margin-bottom: 10px;
      }
      .coin {
        margin-right: 10px;
      }
    }
    p {
      height: 30px;
    }
  }
}
@media screen and (max-width: 420px) {
  .banner {
    flex-direction: column;
    .statistics{
      width: 100%;
    }
    .mid {
      width: 100%;
    }
  }
}
</style>